import React from "react"

export enum MenuIconType {
    REACT = 'REACT',
    TAILWIND = 'TAILWIND'
}

export interface IMenuIconProps {
    //children?: React.ReactNode,
    iconType: MenuIconType
}

const MenuIcon = (
    {
        iconType
    }: IMenuIconProps
) => {

    const form: {[T in MenuIconType]: React.ReactElement} = {
        [MenuIconType.REACT]: <img alt={'react-icon'} className={"w-[20px] opacity-60"} src={"./image/react.svg"} />,
        [MenuIconType.TAILWIND]: <div />

    }

    return (
        form[iconType]
    )
}

export default MenuIcon